import request from '@/utils/request'
// import { getToken } from '@/utils/storage'

// 封装接口，获取文章列表
export const getArticle = (obj) => {
  // const token = getToken()
  return request.get('interview/query', {
    params: {
      current: obj.current || 1, // 当前页
      pageSize: obj.pageSize || 10, // 每页条数
      sorter: obj.sorter // 排序字段 =>  传"weight_desc" 获取 推荐， "不传" 获取 最新
    }
    // headers: {
    //   // 注意 Bearer 和 后面的空格不能删除，为后台的token辨识
    //   Authorization: `Bearer ${token}`
    // }
  })
}

// 封装接口 获取文章详情
export const getArticleDetial = (id) => {
  return request.get('interview/show', {
    params: {
      id
    }
  })
}

// 封装接口 点赞/取消点赞状态
export const updateLike = (id) => {
  return request.post('interview/opt', {
    id: id,
    optType: 1
  })
}

// 封装接口 收藏/取消收藏状态
export const updateCollect = (id) => {
  return request.post('interview/opt', {
    id: id,
    optType: 2
  })
}
// 获取我的收藏
export const getArticlesCollect = (obj) => {
  return request.get('interview/opt/list', {
    params: {
      page: obj.page, // 当前页
      pageSize: 5, // 可选
      optType: 2 // 表示收藏
    }
  })
}

// 获取我的喜欢
export const getArticlesLike = (obj) => {
  return request.get('interview/opt/list', {
    params: {
      page: obj.page, // 当前页
      pageSize: 5, // 可选
      optType: 1 // 表示收藏
    }
  })
}
